import React, { FC, useRef } from 'react';
import useOnClickOutside from '../../../hooks/useClickOutside';
import { colors } from '../../../utils/colors';
import CrossIcon from '../Icons/CrossIcon/CrossIcon';
import { ModalCloseButton, ModalFullPageWrapper, StyledModal } from './styled';

interface Props {
  onClose: () => void;
  showCloseButton?: boolean;
  children: React.ReactNode;
  noPadding?: boolean;
  closeButtonColor?: string;
}

export const Modal: FC<Props> = ({
  children,
  onClose,
  showCloseButton,
  noPadding,
  closeButtonColor,
}) => {
  const modalRef = useRef(null);
  useOnClickOutside(modalRef, onClose);

  return (
    <ModalFullPageWrapper>
      <StyledModal ref={modalRef} noPadding={Boolean(noPadding)}>
        {showCloseButton && (
          <ModalCloseButton onClick={onClose} color={closeButtonColor}>
            <CrossIcon color={colors.primary.black}></CrossIcon>
          </ModalCloseButton>
        )}
        {children}
      </StyledModal>
    </ModalFullPageWrapper>
  );
};
